/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.product-item-admin {
	margin-top: 16px;
	font-size: 13px;
}

.product-item-box {
	display: flex;
	padding: 30px 25px;
	position: relative;
	background: #fff;
	box-shadow: 0 0 10px #eee;
	border-radius: 5px;
}

.product-item-title {
	font-size: 19px;
	line-height: 1.4;
	margin-bottom: 13px;
}

.product-item-thumb {
	width: auto;
	min-width: 16%;
}

.product-item-content {
	width: auto;
	padding-left: 24px;
}

.rakuten-item-snippet {
	font-size: 16px;
	margin-bottom: 13px;
}

.product-item-maker {
	margin-bottom: 5px;
}

.product-item-price {
	font-size: 16px;
	font-weight: bold;
	margin-bottom: 5px;

	.item-price {
		font-size: 18px;
		color: #e60012;
		margin-right: 5px;
	}
}

.product-item-description {
	font-size: 14px;
	margin-bottom: 19px;
}

.product-item-buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
}

.product-item-buttons a {
	display: flex;
	padding: 6px 12px;
	text-decoration: none;
	font-size: 16px;
	font-weight: bold;
	color: #fff;
	background: #222;
	border-radius: 3px;
}

.product-item-buttons a img[decoding="async"] {
	display: none;
}

.product-item-buttons .shoplinkamazon a {
	background: #f79901;
}

.product-item-buttons .shoplinkrakuten a {
	background: #bf0000;
}

.product-item-buttons .shoplinkyahoo a {
	background: #e60033;
}

.product-item-buttons .shoplinkmercari a {
	background: #4dc9ff;
}

.product-item-buttons .shoplinkdmm a {
	background: #00bcd4;
}

// レスポンシブ対応
@media (max-width: 768px) {
	.product-item-box {
		flex-direction: column;
		padding: 20px 15px;
	}

	.product-item-thumb {
		width: 100%;
		min-width: 100%;
		margin-bottom: 15px;
		text-align: center;
	}

	.product-item-content {
		width: 100%;
		padding-left: 0;
	}

	.product-item-title {
		font-size: 17px;
	}

	.product-item-buttons {
		gap: 7px;
	}

	.product-item-buttons a {
		font-size: 14px;
		padding: 5px 10px;
	}
}